import { Component, OnInit } from '@angular/core';
import { LoadingController,AlertController,ModalController, Platform} from '@ionic/angular';

declare const cordova: any;

import * as moment from 'moment';

@Component({
    selector: 'app-pay',
    templateUrl: './pay.page.html',
    styleUrls: ['./pay.page.scss'],
})
export class PayPage implements OnInit {
    
    
    public card:any;
    public loading:boolean;
    public error:string;
    public maxDate: number;
    
    constructor(public modalController: ModalController, public loadingController: LoadingController, public alertController: AlertController,
        private platform: Platform) { 
    
        this.card = {};
        
        
        this.maxDate = (new Date()).getFullYear() + 10;
        
    }

    ngOnInit() {
    }


    public dismiss(){
        this.modalController.dismiss();
    }


    public makePayment(){
        this.loading = true;
        this.error = "";
        let card = {
            number:this.card.number,
            name:this.card.name,
            cvc:this.card.cvc,
            expMonth:moment(this.card.expiry).format("MM"),
            expYear: moment(this.card.expiry).format("YYYY")
        }
        
        cordova.plugins.stripe.createCardToken(card, (data) => {
            this.loading = false;
            this.modalController.dismiss(data);
        }, (e) => {
            this.error = e;
            this.loading = false;
        })
        
        
    }    
    


}
