import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';

@Injectable({
    providedIn: 'root'
})
export class MessageService {

    constructor(public http: HttpClient, public storage: Storage) { }
    
    
    public getMessages(streamId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/messages/" + streamId + "?token=" + token).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }       
    
    public createMessage(streamId, message, sentAt){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/messages?token=" + token, {stream_id:streamId, message:message, sent_at:sentAt}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
    
}
