import { Component, OnInit } from '@angular/core';
import { AlertController, LoadingController, ModalController,NavController,ActionSheetController, Platform, NavParams } from '@ionic/angular';
import {ReviewService} from '../../services/review/review.service';

@Component({
    selector: 'app-create-review',
    templateUrl: './create-review.page.html',
    styleUrls: ['./create-review.page.scss'],
})
export class CreateReviewPage implements OnInit {
    
    
    public review:any;
    constructor(private modalController: ModalController, private reviewService:ReviewService, private alertCtrl:AlertController, private navParams: NavParams) { 
        let gig = this.navParams.get('gig');
        let userId = this.navParams.get('user_id');
        
        this.review = {gig_id:gig.id, user_id:userId, comments:""};
    }

    ngOnInit() {
    }
    
    public dismiss(){
        this.modalController.dismiss();
    } 
    
    public createReview(){
        this.review.loading = true;
        console.log(this.review);
        this.reviewService.createReview(this.review).then(() => {
            this.review.loading = false;
            
            let alert = this.alertCtrl.create({
                header: 'Review Sent!',
                message: 'Your review has been sent and will be displayed on the users profile.',
                buttons: [
                    {
                        text: 'Dismiss',
                        role: 'cancel',
                        handler: data => {
                        }  
                    }
                ] 
            }).then((el) => {
                el.present();
            });     
            
            this.modalController.dismiss({reviewed:true});     
            
            
        }).catch(() => {
            this.review.loading = false;
            
            let alert = this.alertCtrl.create({
                header: 'Error',
                message: 'There was an error submitting this review. Please try again soon.',
                buttons: [
                    {
                        text: 'Dismiss',
                        role: 'cancel',
                        handler: data => {
                        }  
                    }
                ] 
            }).then((el) => {
                el.present();
            });             
            
            
        })
    }
    
    public commentChanged(ev){
        this.review.comments = ev.detail.value;
    }
}
