import { Component, OnInit, NgZone, ViewChild } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController, Platform } from '@ionic/angular';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {GigService} from '../../services/gig/gig.service';
import {StreamService} from '../../services/stream/stream.service';
import {MessageService} from '../../services/message/message.service';
import {PayPage} from '../pay/pay.page';
import {CreateReviewPage} from '../create-review/create-review.page';
