import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';


@Injectable({
    providedIn: 'root'
})
export class StreamService {

    constructor(public http: HttpClient, public storage: Storage) { }
    
    
    public getStream(gigId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + "/streams/" + gigId + "?token=" + token).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }       
    
    public createStream(gigId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/streams?token=" + token, {gig_id:gigId}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }   
    
    
        
    public createParticipant(streamId, peerId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + "/streamparticipants?token=" + token, {stream_id:streamId, peer_id:peerId}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }   
             
    public deleteParticipant(streamId){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.delete(environment.apiUrl + "/streamparticipants/" + streamId + "?token=" + token).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }  
    
    
    public saveDuration(streamId, duration){
        return new Promise((resolve, reject) => {
            this.storage.get("mb_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + "/streams/" + streamId + "?token=" + token, {actual_duration:duration}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
    }     
    
        
    
}
